
import Scroll from '../../../base/scroll/scroll'
import Step from '../../../base/step/step'
import MButton from '../../../base/button/button'
import Info from '../../../base/info/info'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import md5 from 'vux/src/tools/md5/index.js'

import { mapGetters } from 'vuex'
export default {
name: "updateUserPwd",
components: {
Scroll,
XInput,
Group,
Step,
MButton,
Info
},
watch: {
userMsg(val) {
if (val.length == 6) {
this.btnNextState = true
return false
}
this.btnNextState = false
},
pwd(val) {
this.checkState()
},
rePwd(val) {
this.checkState()
}
},
computed: {
userPwdPhone: {
get: function() {
let reg = /^(\d{3})\d{4}(\d{4})$/;
let tel = this.userMyPhone.replace(reg, "$1^-^$2");
return tel
},
set: function() {
return
}
},

...mapGetters([
'userPhone'
])
},
created() {
if (this.userPhone.length) {
this.userMyPhone = this.userPhone
} else {
this.userMyPhone = '15345812564'//
}
},
data() {
return {
stepArray: ['', ''],
infos: [
'',
''
],
userMyPhone: '',
stepIndex: 0,//
btnCodeState: true,
btnNextState: false,
btnSaveState: false,
userMsg: '',
disabled: '',
time: 60,
codeText: '',
timer: null,
pwd: '',//
rePwd: '',
pwdType: (val) => {
let reg = new RegExp(/((?=.*\d)(?=.*\D)|(?=.*[a-zA-Z])(?=.*[^a-zA-Z]))^.{6,16}$/);
if (reg.test(val)) {
return {
valid: true
}
}
return {
valid: false,
msg: ''
}
},
rePwdType: (val) => {
if (val === this.pwd) {
return {
valid: true
}
}
return {
valid: false,
msg: ''
}
},
}
},
methods: {

getCode() {
if (this.btnCodeState) {
this.btnCodeState = false
this.$axios.post('/api/sms/send', {
telephone: this.userMyPhone,
type: 0
}).then((response) => {
if (response.data.code === '200') {
this.timer = setInterval(this.timeGo, 1000)
}
}).catch((error) => {
this.btnCodeState = true
this.$vux.alert.show({
title: '',
content: '',
})
})
}
},

timeGo() {
if (this.time >= 0) {
this.codeText = `${this.time}(s)`
this.time--
} else {
this.codeText = ''
this.time = 60
clearInterval(this.timer)
this.timer = null
this.btnCodeState = true
}
},

checkState() {
if (this.$refs.pwd.valid && this.$refs.rePwd.valid) {
this.btnSaveState = true
return false
}
this.btnSaveState = false
},

next() {
this.$axios.post('/api/sms/verify', {
telephone: this.userMyPhone,
code: this.userMsg,
type: 0
}).then((response) => {
if (response.data.code !== '200') {
this.$vux.alert.show({
title: '',
content: response.data.message,
})
}else{
this.stepIndex = 1
}
}).catch((error) => {
console.log(error)
})
},

save() {
if (!this.btnSaveState) {
return false
}
this.$axios.post('/api/user/resetPassword', {
telephone: this.userMyPhone,
password: md5(this.pwd),
}).then((response) => {
let data = response.data
console.log(data)
if (data.code !== "200") {
this.$vux.alert.show({
title: '',
content: data.message,
})
} else {
let _this = this
this.$vux.toast.show({
text:'',
type:'success',
time:1000,
onHide(){
_this.$router.push({ name: 'user' })
}
})
}
}).catch((error) => {
this.$vux.alert.show({
title: '',
content: '',
})
})
}
}
}



